import React, {useState, useEffect} from 'react';
import {base_url, public_url, $http} from "config";
import {NotificationContainer, NotificationManager} from 'react-notifications';
import ContainerHeader from 'components/ContainerHeader/index';
import IntlMessages from 'util/IntlMessages';
import CardBox from 'components/CardBox/index';
import Checkbox from '@material-ui/core/Checkbox';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import {useHistory} from 'react-router-dom';
import {useSelector} from 'react-redux';
import CircularProgress from '@material-ui/core/CircularProgress';
import Fab from '@material-ui/core/Fab';
import SweetAlert from 'react-bootstrap-sweetalert'
import Resizer from 'react-image-file-resizer';

const CustomerModify = (props) => {
    const History = useHistory();
    const customer_id = props.location.state ? props.location.state.id : null;
    const [loader, setLoader] = useState(false);
    const [name, setCompanyName] = useState("");
    const [account_manager, setAccountManager] = useState("");
    const [website, setWebsite] =  useState("");
    const [company_email, setCompanyEmail] = useState("");
    const [address, setAddress] = useState("");
    const [city, setCity] = useState("");
    const [postcode, setPostcode] = useState("");
    const [is_site, setIsSite] = useState(props.location.state ? false : true);
    const [logo_img, setLogoImg] = useState(null);
    const {width} = useSelector(({settings}) => settings);
    const [imagePreviewUrl, setLogoImgUrl] = useState('https://via.placeholder.com/300x300');

    const [contact_name, setContactName] = useState([]);
    const [goback, setGoback] = useState(false)

    useEffect(() => {
        $http.get(base_url + "customers/company/customer-edit", {params: {id: customer_id}})
        .then((response) => {
            if (response.data.company) {
                setCompanyName(response.data.company.name);
                setAccountManager(response.data.company.manager);
                setWebsite(response.data.company.website);
                setCompanyEmail(response.data.company.company_email);
                setAddress(response.data.company.address);
                setCity(response.data.company.city);
                setPostcode(response.data.company.postcode);
                setLogoImgUrl(response.data.company.logo_img ? 
                    (public_url + 'upload/img/' + response.data.company.logo_img) : 
                    'https://via.placeholder.com/300x300');
            }
            setContactName(response.data.account_manager);
            setLoader(true);
        })
        .catch((error) => {
            setGoback(true)
        })
    }, [customer_id]);
    const SaveCustomer = () => {
       
        let offline_id = customer_id > 0 ? customer_id:JSON.parse(localStorage.getItem('user')).first_name+'_'+new Date().getTime();
        var site_off_id = JSON.parse(localStorage.getItem('user')).first_name+'_'+new Date().getTime();
        if (!navigator.onLine){
            caches.open('SirvezApp').then(function(cache){
                let fileName = ''
                if(logo_img)
                {
                    fileName = new Date().getTime()+'_new.jpeg';
                    cache.put('/upload/img/'+fileName, new Response(logo_img));
                }
                var modify_data = {
                    id : offline_id,
                    site_off_id : site_off_id,
                    name : name,
                    city : city,
                    postcode : postcode,
                    company_email : company_email,
                    address : address,
                    logo_img : fileName,
                    manager : account_manager,
                    website : website,
                    project_count : 0,
                    room_count : 0,
                    site_count : (is_site?1:0),
                    user_count : 0
                }   
                //save company_edit
                cache.match('/api/customers/company/customer-edit')
                .then(function(response) {return response ? response.json() : null})
                .then(function(data){
                    if(data==null) return
                    data.company = modify_data
                    const jsonResponse = new Response(JSON.stringify(data), {
                        headers: {
                            'content-type': 'application/json'
                        }
                    });
                    cache.put('/api/customers/company/customer-edit?id='+offline_id, jsonResponse.clone());
                })
                
                //save into list
                const requestUrl = '/api/customers/company';
                cache.match(requestUrl)
                .then(function(response) {return response ? response.json() : null})
                .then(function(data){
                    if(data==null) return
                    let item = data.customers.find((item) => {return (item.id === offline_id)})
                    if (item) {
                        item.name = name
                        item.city = city
                        item.postcode = postcode
                        item.company_email = company_email
                        item.address = address
                        item.manager = account_manager
                        item.website = website
                        if(fileName!=='') item.logo_img = fileName
                    }
                    else
                    {
                        data.customers.unshift(modify_data)
                    }
                    const jsonResponse = new Response(JSON.stringify(data), {
                        headers: {'content-type': 'application/json'}
                    });
                    cache.put(requestUrl, jsonResponse.clone());
                })
                //save company_info
                let cashe_data = {
                    company:modify_data,
                    projects : [],
                    rooms : [],
                    sites : [],
                    status : "success",
                    tasks : [],
                    users : [],
                }
                if(customer_id>0){
                    cache.match("/api/customers/companyInfo?id="+customer_id)
                    .then((response)=>response.json())
                    .then(function(data){
                        cashe_data.projects = data.projects
                        cashe_data.rooms = data.rooms
                        cashe_data.sites = data.sites
                        cashe_data.tasks = data.tasks
                        cashe_data.users = data.users
                    })
                }
                else if(is_site){
                    
                    var site = {
                        id:site_off_id,
                        company_id:offline_id,
                        site_name : name + 'Head Office',
                        address : address,
                        city:city,
                        postcode:postcode,
                        rooms:0,
                        projects:0
                    }
                    //site-editmodal
                   
                    let saving_data = {site : site}
                    const jsonResponse = new Response(JSON.stringify(saving_data), {
                        headers: {
                            'content-type': 'application/json'
                        },
                        method:'GET'
                    });
                    cache.put('/api/site/siteInfo?id='+site_off_id, jsonResponse.clone());
                    

                    cashe_data.sites[0] = site;
                }
                const jsonResponse = new Response(JSON.stringify(cashe_data), {
                    headers: {
                        'content-type': 'application/json'
                    },
                    method:'GET'
                });
                cache.put("/api/customers/companyInfo?id="+offline_id, jsonResponse.clone());

                const requestUrl_Task = '/api/task/taskList?customer_id=' + offline_id
                if (!customer_id) {
                    cache.match('/api/task/taskList')
                    .then(function(response) {return response ? response.json() : null})
                    .then(function (data) {
                        if (data == null) return
                        data.project_id = ''
                        data.customerId = offline_id
                        data.tasks = []
                        const jsonResponse = new Response(JSON.stringify(data), {
                            headers: {'content-type': 'application/json'}
                        })
                        cache.put(requestUrl_Task, jsonResponse.clone())
                    })
                    .catch(function(err){
                        let data = {
                            customers :[],
                            projects:[],
                            status:'success',
                            tasks:[],
                            users:[]
                        }
                        const jsonResponse = new Response(JSON.stringify(data), {
                            headers: {'content-type': 'application/json'}
                        })
                        cache.put(requestUrl_Task, jsonResponse.clone())
                    })
                    
                }

            }).catch((err)=>{
                console.log('cashe_error',err);
            })
        }
       
        const frmData = new FormData();
        frmData.append('id', offline_id);
        frmData.append('site_off_id', site_off_id);
        frmData.append('company_name', name);
        frmData.append('logo_img', logo_img);
        frmData.append('website', website);
        frmData.append('company_email', company_email);
        frmData.append('address', address);
        frmData.append('city', city);
        frmData.append('postcode', postcode);
        frmData.append('is_site', is_site);
        frmData.append('manager', account_manager);
        $http.post(base_url + "customers/company/customer-edit", frmData)
        .then((response) => {
            if (response.data.status === "success") {
                // History.push({
                //     pathname: '/app/customers/company/info',
                //     state: {id: response.data.id}
                // })
                History.go(-1)
            } else if (response.data.status === "error") {
                NotificationManager.error(response.data.msg, "Error!", 1000, () => {
                    alert('callback');
                });
            }
        }).catch((error)=>{
            NotificationManager.error(error, "Error!", 1000, () => {
            });
        })
    };
    const resizeFile = (file) => new Promise(resolve => {
        Resizer.imageFileResizer(file, 500, 500, 'JPEG', 100, 0,
        uri => {
          resolve(uri);
        },
        'blob'
        );
    });
    const _handleImageChange = async (e) =>{
        e.preventDefault();
  
        //let reader = new FileReader();
        let file = e.target.files[0];
        const image = await resizeFile(file);
        let img_file_name = file.name
        file = new File([image],img_file_name,image)
        setLogoImg(file);
        setLogoImgUrl(URL.createObjectURL(file));
        // reader.onloadend = () => {
        //     setLogoImg(file);
        //     setLogoImgUrl(reader.result);
        // }
        // reader.readAsDataURL(file)
    }
    return (
        <div className="app-wrapper">
            <ContainerHeader match={props.match} title={<IntlMessages id="sidebar.customers.company"/>}/>
            { loader ? (
                <div className="row">
                    <div className="col-xl-3 col-lg-4 col-md-5 col-12">
                        <div className="jr-card pb-2">
                            <div className="jr-card-thumb">
                                <img className="card-img-top img-fluid" alt="products" src={imagePreviewUrl}/>
                            </div>
                            <input type="file" id="logo_img" accept="image/*" 
                                name="logo_img" style={{display: "none"}}
                                onChange={(event) => {
                                    _handleImageChange(event);
                                }}/>

                            <div className="jr-card-social text-right">
                                <Button className="jr-btn text-primary">
                                    Company Logo
                                </Button>
                                <Fab className="jr-fab-btn bg-light-blue accent-2 text-white" onClick={(e)=>{document.getElementById('logo_img').click()}}>
                                    <i className="zmdi zmdi-cloud-upload zmdi-hc-2x"/>
                                </Fab>
                            </div>
                        </div>
                    </div>
                    <CardBox styleName="col-xl-9 col-lg-8 col-md-7 col-12">
                        <form className="row" autoComplete="off" >
                            <div className="col-md-6 col-12">
                                <TextField 
                                    id="name"
                                    label="Company Name"
                                    value={name}
                                    onChange={(event)=>setCompanyName(event.target.value)}
                                    margin="normal"
                                    helperText="Please Enter Company Name"
                                    fullWidth
                                    />
                            </div>
                            <div className="col-md-6 col-12">
                                <TextField
                                    id="account_manager"
                                    select
                                    label="Account Manager"
                                    value={account_manager}
                                    onChange={(event)=>setAccountManager(event.target.value)}
                                    SelectProps={{}}
                                    helperText="Please select Account Manager"
                                    margin="normal"
                                    fullWidth
                                >{contact_name && contact_name.length > 0 ?
                                    contact_name.map(option => (
                                        <MenuItem key={option.id} value={option.id}>
                                            {option.first_name} {option.last_name}
                                        </MenuItem>
                                    )):''
                                }
                                </TextField>
                            </div>
                            <div className="col-md-6 col-12">
                                <TextField 
                                    id="website"
                                    label="Website Url"
                                    value={website}
                                    onChange={(event)=>setWebsite(event.target.value)}
                                    margin="normal"
                                    helperText="Please Enter Website"
                                    fullWidth
                                    />
                            </div>
                            <div className="col-md-6 col-12">
                                <TextField 
                                    id="company_email"
                                    label="Company Email"
                                    value={company_email}
                                    onChange={(event)=>setCompanyEmail(event.target.value)}
                                    margin="normal"
                                    helperText="Please Enter Company Email"
                                    fullWidth
                                    />
                            </div>
                            <div className="col-md-6 col-12">
                                <TextField 
                                    id="address"
                                    label="Address"
                                    value={address}
                                    onChange={(event)=>setAddress(event.target.value)}
                                    margin="normal"
                                    helperText="Please Enter Address"
                                    fullWidth
                                    />
                            </div>
                            <div className="col-md-6 col-12">
                                <TextField 
                                    id="city"
                                    label="City"
                                    value={city}
                                    onChange={(event)=>setCity(event.target.value)}
                                    margin="normal"
                                    helperText="Please Enter City"
                                    fullWidth
                                    />
                            </div>
                            <div className="col-md-6 col-12">
                                <TextField 
                                    id="postcode"
                                    label="Postcode"
                                    value={postcode}
                                    onChange={(event)=>setPostcode(event.target.value)}
                                    margin="normal"
                                    helperText="Please Enter Postcode"
                                    fullWidth
                                    />
                            </div>
                            {
                                customer_id > 0 || (
                                    <div className="d-flex align-items-center">
                                        <Checkbox 
                                            id="is_site"
                                            color="primary"
                                            checked={is_site}
                                            onChange={(event)=>setIsSite(event.target.checked)}
                                        /> 
                                        <span>Create Site customer address</span>
                                    </div>
                                )
                            }
                            <div className="col-md-12 col-12 text-right">
                                <Button variant="contained" color="primary" className="jr-btn"
                                    onClick={()=>SaveCustomer()}
                                >
                                    Save
                                </Button>
                            </div>
                        </form>
                    </CardBox>
                </div>
            ) : (
                <div className="loader-view"
                        style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
                    <CircularProgress/>
                </div>
            )}
                <SweetAlert show={goback}
                    warning
                    confirmBtnText="Go Back"
                    confirmBtnBsStyle="danger"
                    cancelBtnBsStyle="default"
                    title="Warning!"
                    onConfirm={() => History.go(-1)}
                >                
                Data not cached, so you can not view this page.
                </SweetAlert>
                <NotificationContainer/>
        </div>
    );
}

export default CustomerModify;